@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,500;1,300;1,400;1,500&display=swap');

*{
  margin:0;
  padding:0;

}
.App {
  text-align: center;
  font-family: 'Open Sans', sans-serif;
}
body{
  background-color: #f6faff;
}
.navbar{
  height: 6vh;
  background-color: white;
  display: flex;
  justify-content: space-between;
  font-family: 'Open Sans', sans-serif;
  font-size: 1.1em;
  font-weight: 350;
  box-shadow: 0px 2px 3px rgba(0, 0, 0, .5);
}
.navbar-sign-in-out{
  width: 20%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-right: 1%;
}
.navbar-sign-in-out a{
  text-decoration: none;
  color:black;
  border:1px solid rgba(0, 0, 0, .5);
  padding:5px 25px;
  font-weight: 550;
  transition: all 200ms;
}
.navbar-sign-in-out a:hover{
  background-color: rgb(0, 0, 0);
  color:white;
}
.navbar-btns{
  height: 100%;
  width: 10%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-left: 2%;
}
.navbar-btns a{
  color:black;
  text-decoration: none;
}
.signInPage-div{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top:5%;
}
.gradient-div{
  width: 40%;
  height: 80vh;
  background: linear-gradient(65deg, #dcc2fd, #71b7fd, #adc2f6);
  border-top-left-radius: 8px;
  border-bottom-left-radius: 8px;
}
.sign-in-div{
  width: 40%;
  height: 80vh;
  background-color: white;
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
  font-family: 'Open Sans', sans-serif;
}
.sign-in-div h2{
  margin-top:10%;
  margin-left: 15%;
}
.sign-in-div p{
  margin-top: 1%;
  margin-left: 15%;
}
.sign-in-inputs{
  height: 50%;
  width: 55%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-left: 15%;
}
.sign-in-inputs p{
  margin-top: 2%;
  margin-left: 0%;
}
.input-fields{
  width: 100%;
  height: 40%;
  margin-top: 5%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.input-fields input{
  padding:8px 13px;
  border:1px solid rgba(0, 0, 0, 1);
  border-radius: 5px;
  font-size: 1.1em;
}
.sign-in-btn{
  background:linear-gradient(to right, #5fadfb, #615efa, #9a8cfc) ;
  width: 90%;
  margin:auto;
  font-size: 1.5em;
  color:white;
  border:none;
  border-radius: 8px;
  padding:12px 30px;
  cursor: pointer;
}
.border-div{
  height: 1px;
  width: 55%;
  margin-left:15%;
  margin-bottom: 5%;
  background-color: black;
}
.google-btn{
  width: 40%;
  margin-left:19%;
  padding:12px 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  border:1px solid rgba(0, 0, 0, .5);
  border-radius: 8px;
  cursor: pointer;
}
.google-icon{
  position: inline-block;
  vertical-align: middle;
  margin-right: 10px;
}
.btn-text{
  position:inline-block;
  vertical-align: middle;
}
.createPost-container{
  width: 50%;
  height: 80vh;
  margin:auto;
  margin-top:5%;
  font-size: 1.2em;
  background-color: white;
  border-radius: 8px;
  font-family: 'Open Sans', sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.createPost-container h1{
  margin-top:10px;
  margin-bottom:5%;
}
.cp-inputs{
  height: 100%;
  width: 80%;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.input-cp{
  width: 75%;
  display: flex;
  align-items: center;
  margin:10px;
  margin-left:10%;
}
.input-cp label{
  margin-right: 25px;
}
.input-cp:last-child{
  display: flex;
  align-items: flex-start;
}
.input-cp input{
  width: 410px;
  font-size: .9em;
  border:1px solid black;
  border-radius: 5px;
  padding:5px 10px;
}
.input-cp input:focus{
  outline:none;
}
.input-cp textarea{
  font-size: 1em;
  border:1px solid black;
  border-radius: 5px;
  padding:5px 10px;
  height: 450px;
  max-height: 450px;
  width: 409px;
  max-width: 409px;
}
.input-cp textarea:focus{
  outline:none
}
.button-wrapper{
  width: 90%;
  height: 50%;
  margin-left: 6%;
  display: flex;
}
.submit-blog{
  background:linear-gradient(to right, #5fadfb, #615efa, #9a8cfc) ;
  width: 50%;
  margin:auto;
  font-size: 1.5em;
  color:white;
  border:none;
  border-radius: 8px;
  padding:12px 30px;
  cursor: pointer;
}
.home-page-container{
  width: 50%;
  margin:auto;
  margin-top:2%;
  font-family: 'Open Sans', sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.home-page-container h1{
  margin-bottom: 5%;
}
.post{
  width: 60%;
  margin-bottom:5%;
  display: flex;
  flex-direction: column;
  align-items:center;
  position:relative;
  border: 1px solid rgba(0, 0, 0, .5);
  border-radius: 5px;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, .5);
  padding:20px 30px;
}
.delete-btn{
  width: 30px;
  background: none;
  border:none;
  position: absolute;
  top:10px;
  right:10px;
  cursor: pointer;
}
#blog{
  margin-bottom: 10%;;
}
#author{
  position:absolute;
  bottom:10px;
  left:10px;
}
.hamburger-menu{
  display: none;
}
.sidebar{
  display: none;
}
@media(max-width:1280px){
  html{
    max-width: 100vw;
    overflow-x: hidden;
  }

  #root{
    max-width: 93vw;
  }
  .navbar{
    width: 100vw;
    height: 10vh;
  }
  .navbar-btns{
    width: 15%;
  }
  .navbar-sign-in-out{
    margin-right: 2%;
    width: 25%;
  }
  .navbar-sign-in-out a{
    padding:5px 5px;
  }
}
@media(max-width:1024px){
  .navbar-sign-in-out{
    margin-right: 1%;
    width: 40%;
  }
  .navbar-btns{
    width: 20%;
  }
}
@media(max-width:912px){
  .navbar a, .navbar p{
    font-size: 1.5em;
  }
  .navbar-btns{
    width: 35%;
  }
  .navbar-sign-in-out{
    width: 55%;
  }
  .home-page-container{
    /* background-color: red; */
    width: 90%;
  }
  #blog{
    margin-bottom: 20%;
  }
  .post p{
    font-size: 1.5em;
  }
}
@media(max-width:540px){
  html{
    overflow-x: hidden;
  }
  .navbar{
    width: 100vw;
    height: 7vh;
    position:relative;
  }
  .navbar a, .navbar p{
    font-size: 1em;
  }
  .navbar-btns, .navbar-sign-in-out a, .navbar-sign-in-out p{
    display: none;
  }
  .navbar-sign-in-out{
    position:absolute;
    right: 0px;
    display: flex;
    justify-content: flex-end;
  }
  .hamburger-menu{
    height: 22px;
    width: 30px;
    display: block;
  }
  .hamburger-menu ul{
    height: 100%;
    list-style: none;
    margin:0;
    padding:0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .hamburger-menu li{
    height: 4px;
    margin:1px;
    width: 100%;
    background-color: black;
  }
  .sidebar-btn{
    background: none;
    border:none;
  }
  .sidebar{
    height: 93vh;
    width: 65%;
    position: absolute;
    right: 0px;
    background-color: #f6faff;
    box-shadow: 0px 1px 3px black;
    font-family: 'Open Sans', sans-serif;
    z-index: 2;
    display: none;
    flex-direction: column;
    justify-content: space-between;
  }
  .active{
    display: flex;
  }
  .sidebar-btns{
    /* background-color: blue; */
    height: 15%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
  }
  .sidebar-btns a{
    color:black;
    text-decoration: none;
    text-align: center;
    padding:10px 15px;
    background-color: rgba(255, 255, 255, .5);
  }
  .sidebar-sign-in-out{
    height: 10%;
    margin-bottom: 5%;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .sidebar-sign-in-out a{
    color:black;
    text-decoration: none;
    border:1px solid black;
    padding:5px 10px;
    margin-left: 5px;
  }
  .sidebar-sign-in-out p{
    margin-right: 5px;
  }
  .home-page-container{
    margin-left: 10%;
  }
  .post{
    width: 90%;
  }
  .post h2{
    font-size: 1.4em;
  }
  #blog , #author{
    font-size: 1.1em;
  }
}
@media(max-width:414px){
  html, body{
    margin:0 auto;
    overflow-x: hidden;
  }
  .sidebar{
    width: 90%;
  }
  .home-page-container{
    margin-left: 9%;
  }
  .post{
    width: 100%;
  }
  .delete-btn{
    width: 25px;
  }
}
@media(max-width:360px){
 .post{
  width: 95%;
 }
 .delete-btn{
  right: 1px;
  top:1px;
 }
 .post h2{
  font-size: 1.3em;
 }
}